import App from './src/views/App.vue'

// 
import * as routeList0 from './src/views/modules/account/route.js'; import * as routeList1 from './src/views/modules/index/route.js'; import * as routeList2 from './src/views/modules/user/route.js'; let routeList = [routeList0, routeList1, routeList2]

// vuex store
import * as storeList0 from './src/views/layout/mainStore.js'; import * as storeList1 from './src/views/modules/account/mainStore.js'; let storeList = [storeList0, storeList1]

// 
Vue.use(VueRouter)
// vuex
Vue.use(Vuex)

let routes = []

routeList.map((item) => {
  routes = routes.concat(item.default)
})

let stores = {}
storeList.map((item) => {
  stores = Object.assign(stores, item.default)
})

const router = new VueRouter({
  routes
})

// Vue 
let vm = null

Vue.prototype.KeepAlive = false

const processRoute = (vm, to) => {
  if (Vue.prototype.KeepAlive) {
    Vue.prototype.KeepAlive = false
  } else {
    const that = vm
    that.$store.dispatch('Layout/Header/SET_OBJ', { name: to.name })
    setTimeout(() => {
      that.$store.dispatch('Layout/Header/SET_OBJ', { name: '' })
    }, 0)
  }
}

Vue.prototype.mixin = {
  beforeRouteLeave(to, from, next) {
    processRoute(this, to)
    next()
  },
  beforeRouteUpdate(to, from, next) {
    processRoute(this, to)
    next()
  },
  methods: {
    Npush: (vm, location) => {
      vm.$router.push(location)
    }
  }
}

// 
router.beforeEach(async (to, from, next) => {

  setTimeout(() => {
    vm && vm.$store.dispatch('Layout/Header/SET_TAB', to)
    vm && vm.$store.dispatch('Layout/Header/SET_NAMELIST', to)
  }, 0)
  
  next()
})

router.afterEach((to, from) => {

  console.log('afterEach')

})

const store = new Vuex.Store({
  modules: stores,
  strict: true,
})

VuexRouterSync.sync(store, router, { moduleName: 'RouteModule' })

// ElementUI
Vue.use(ElementUI)

vm = new Vue({
  router,
  store,
  el: '#app',
  render: h => h(App)
})