
// store
const initState = {
  name: 'AccountList',
}

export default {
	AccountList: {
		namespaced: true,

		// state
		state: Object.assign({}, initState),

		// actions
		actions: {

			// state
			INIT_STATE({commit}) {
			    commit('INIT_STATE', initState)
			},

			// 
      SET_NAME({ commit }, payload) {
        commit('SET_NAME', payload)
			},
			
		},

		// mutations
		mutations: {
			// state
		    INIT_STATE (state, payload) {
		    	Object.assign(state, payload)
		    },

        SET_NAME (state, payload) {
		    	state.name = payload.name
		    },		    
		    
		},

		// getters
		getters: {

		}
	}
}